}
@-webkit-keyframes fade-in {
  0% {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
  1% {
    display: block;
    visibility: visible;
    opacity: 0;
  }
  100% {
    display: block;
    visibility: visible;
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
  1% {
    display: block;
    visibility: visible;
    opacity: 0;
  }
  100% {
    display: block;
    visibility: visible;
    opacity: 1;
  }
}
@-webkit-keyframes fade-out {
  0% {
    display: block;
    visibility: visible;
    opacity: 1;
  }
  99% {
    display: block;
    visibility: visible;
    opacity: 0;
  }
  100% {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
}
@keyframes fade-out {
  0% {
    display: block;
    visibility: visible;
    opacity: 1;
  }
  99% {
    display: block;
    visibility: visible;
    opacity: 0;
  }
  100% {
    display: none;
    visibility: hidden;
    opacity: 0;
  }
}
