  letter-spacing: 0.0375em;
}
.post-footer a {
  color: currentColor !important;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(75%, rgba(5, 5, 38, 0.2)),
    color-stop(0, rgba(5, 5, 38, 0.2))
  ) !important;
  background-image: linear-gradient(
    180deg,
    rgba(5, 5, 38, 0.2) 75%,
    rgba(5, 5, 38, 0.2) 0
  ) !important;
  background-image: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(75%, rgba(var(--fg), 0.2)),
    color-stop(0, rgba(var(--fg), 0.2))
  ) !important;
  background-image: linear-gradient(
    180deg,
    rgba(var(--fg), 0.2) 75%,
    rgba(var(--fg), 0.2) 0
  ) !important;
  background-repeat: repeat-x;
  background-size: 1px 1px;
  background-position: 0 97%;
}
.post-footer hr {
  margin-top: 0;
  margin-bottom: 0;
}
.post-footer p {
  margin-bottom: 0.75rem;
}
.post-footer ol,
.post-footer li:last-of-type,
.post-footer p:last-child {
  margin-bottom: 0;
}
.post-footer pre {
  margin-top: 0;
  margin-bottom: 0.75rem;
}
.post-footer .row {
  margin-top: 0.71875rem;
  margin-bottom: 0.59375rem;
}
.post-footer .col {
  color: rgba(5, 5, 38, 0.5);
  color: rgba(var(--fg), 0.5);
  margin-top: 0.09375rem;
  margin-bottom: 0.09375rem;
}
.post-footer--authors {
  margin-top: 3rem;
  padding-bottom: 1px;
  margin-bottom: -1px;
}
.release-cover {
  background-color: #fff;
  padding: 5%;
}
.release-cover--titled {
  padding: 4% 5% 5%;
}
.last-child-mb-1 > *:last-child {
  margin-bottom: 1.5rem !important;
}
.footer-icon {
  display: block;
}
.footer-icon--twitter {
  width: 19px;
  margin-top: 1px;
}
@media (min-width: 960px) {
  .footer-icon--twitter {
    width: 21px;
  }
}
.footer-icon--facebook {
  width: 17px;
}
@media (min-width: 960px) {
  .footer-icon--facebook {
    width: 19px;
  }
}
.subscribe-email,
.subscribe-button {
  border-width: 1px;
  border-style: solid;
  border-color: rgba(5, 5, 38, 0.15);
  border-color: rgba(var(--fg), 0.15);
}
.subscribe-form .subscribe-email,
.subscribe-form .subscribe-button {
  height: 1.5rem;
}
.subscribe-form--large .subscribe-email,
.subscribe-form--large .subscribe-button {
  height: 2rem;
}
.subscribe-email {
  min-width: 12rem;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-top-left-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  padding-left: 0.4em;
  padding-right: 0.4em;
  border-right-width: 0;
  outline: none;
  background-color: transparent;
  padding-top: 2px;
  padding-bottom: 0;
}
.subscribe-form--large .subscribe-email {
  min-width: 16rem;
}
.subscribe-email:hover {
  background-color: rgba(5, 5, 38, 0.02);
  background-color: rgba(var(--fg), 0.02);
}
.subscribe-email:focus {
  background-color: #fff;
  background-color: rgba(var(--bg), 1);
  border-color: rgba(5, 5, 38, 0.15);
  border-color: rgba(var(--fg), 0.15);
}
.subscribe-button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  padding-left: 0.25em;
  padding-right: 0.06em;
  background-color: #fff;
  background-color: rgba(var(--bg), 1);
}
.video-swap.is-video-swapped,
.video-target {
  max-height: 100vh;
  height: 56.25vw;
}
.video-swap {
  position: relative;
}
.video-trigger {
  position: relative;
  -webkit-transition: -webkit-filter 100ms cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: -webkit-filter 100ms cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: filter 100ms cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: filter 100ms cubic-bezier(0.645, 0.045, 0.355, 1),
    -webkit-filter 100ms cubic-bezier(0.645, 0.045, 0.355, 1);
}
.video-trigger:hover {
  -webkit-filter: brightness(80%);
  filter: brightness(80%);
}
.video-trigger > span {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  -webkit-transform: translate(-0.5rem, -50%);
  transform: translate(-0.5rem, -50%);
  text-align: center;
  color: #fff;
}
.is-video-swapped .video-trigger {
  display: none;
}
.video-trigger .icon {
  font-size: 1.5em;
  margin-right: 0.1875rem;
  position: relative;
  top: 0.45rem;
}
.video-target {
  visibility: hidden;
  position: absolute;
  top: 0;
  z-index: -1;
}
.is-video-swapped .video-target {
  visibility: visible;
  position: unset;
  top: unset;
  z-index: unset;
}
